.BOXimage{
    width: 150px;
    height: 150px;
    margin-top: 7px;
}
.BOXimage img{
    width: 100%;
    height: 100%;
}
.addFile{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

input[type="file"]{
    display: none;
}
.label{
    background-color: rgb(47, 178, 187);
    padding:5px 10px;
    border-radius: 10px;
    color: white;
    font-size: 15px;}
.inputFile{
    padding: 50px;
    border: 4px dashed #ddd;
    margin-top: 10px;
    font-size: 20px;
}
.WraperImage{
    width: 110px;
    height: 60px;
}
.WraperImage img{
    width: 100%;
    height: 100%;
}
input[type="checkbox"]:checked{
    color: red;
}
.txt{
    margin-right: 20px;
}
.cardBoDy{
    border: 3px solid #ccc;
}

.BoxWraper{
    display: flex;
    flex-direction: column;
    width: 110px;
}
.mainWraper{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.isActive{
    opacity: 1;
}